body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    background: #181633;
    min-height: 70px;
    position: fixed;
    width: 100%;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #fff;
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
    z-index: 99;
    white-space: nowrap;
}

.navbar li {
    display: inline-block;
    width: 33.3%;
    text-align: center;
}

.navbar a {
    text-decoration: none;
    color: #fff;
    font-weight: 900;
    font-family: Arial,Helvetica Neue,Helvetica,sans-serif;
}
.header {
    background: -webkit-linear-gradient(left,#cd122d 0,#154284);
    background: linear-gradient(90deg,#cd122d 0,#154284);
    padding: 200px 20px 120px 20px;
    color: #fff;
    text-shadow: 2px 2px #181633
}

.header h1 {
    margin: 0;
    font-size: 4rem;
}

.logo {
    float: left;
    max-width: 200px;
}

.logo2 {
    float: right;
    max-width: 200px;
}
.container {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
}
.player-card > img{
    width: 285px;
    height: 185px;
    background-size: cover;
    border-radius: 10px;
    border: 5px solid #EDBB02;
    margin: 10px 0px;
    cursor: pointer;
    box-shadow: 0 4px 7px rgba(0,0,0,.16), 0 4px 7px rgba(0,0,0,.23);
}

.player-card > img:hover {
    box-shadow: 8px 8px 20px #1C4282;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.footer {
    background: #181633b7;
    height: 100px;
    position: relative;
    color: #fff;
    overflow: auto;
}

.footer>.bottom>img {
    width: 20px;
    position: relative;
    top: 3px;
    left: 2px;
}

.footer>.bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #181633;
    padding: 8px;
    font-weight: 600;
}

a {
    text-decoration: none;
    color: #fff;
}

a:hover {
    color: #EDBB02;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

